<template lang="jade">
  .group-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .stock-list.scroll-content

      
      .form

        div(style="text-align: center")
          .ds-button-group
            .ds-button.x-small.text-button(:class=" { selected: type === 0 } " @click=" type = 0 " ) 
            .ds-button.x-small.text-button(:class=" { selected: type === 1 } " @click=" type = 1 " ) 
        label.item  
          el-date-picker(v-model="st" type="datetime" placeholder="")
          |   
          el-date-picker(v-model="et" type="datetime" placeholder="")
          
        label.item  &nbsp; 
          el-select(v-model="s")
            el-option(v-for="S in STATUS" v-bind:label="S.title" v-bind:value="S")

        .ds-button.primary.large.bold(@click="bonus") 

        el-table.header-bold.nopadding(:data="bonusList" v-bind:row-class-name="tableRowClassName" max-height="400" v-show=" !(type === 0 && me.role < 4)")

          el-table-column(prop="nickName" label="" width="80" v-if="type === 1")

          el-table-column(prop="expireTm" label="" width="140" )

          el-table-column(prop="beginTm" label="" width="140" )

          el-table-column(label="" width="100" align="right")
            template(scope="scope")
              span {{ scope.row.ruleType === 0 ? scope.row.sales : '--' }}

          el-table-column(label="" width="100" align="right")
            template(scope="scope")
              span {{ scope.row.ruleType === 1 ? scope.row.sales : '--' }}

          el-table-column(prop="bouns" label="" width="120" align="right")

          el-table-column(prop="status" label="" align="center" width="80")
             template(scope="scope")
              span(:class="{ 'text-green': scope.row.isDone > 1, 'text-blue': scope.row.isDone < 2 }") {{ STATUS[scope.row.isDone].title }}

          el-table-column(prop="userpoint" label="" align="center")
            template(scope="scope")
              .ds-button.text-button.blue(style="padding: 0 .05rem" @click="goStockDetail(scope.row.id)") 


        el-table.header-bold.nopadding(:data="topBonuList" max-height="400" v-on:expand="expand" v-show=" (type === 0 && me.role < 4)")

          el-table-column(type="expand")
            template(scope="scope")
              el-table.header-bold.nopadding(:data="topDetailList")
                el-table-column(prop="creatTime" label="" width="140" )
                el-table-column(prop="times" label="" width="140" )
                // el-table-column(prop="startDate" label="" width="140" )
                // el-table-column(prop="endDate" label="" width="140" )
                el-table-column(prop="relatName" label="" width="80" )
                // el-table-column(prop="conBeginTm" label="" width="140" )
                // el-table-column(prop="conExPireTm" label="" width="140" )
                el-table-column(prop="bonusRate" label="" width="80" )
                el-table-column(prop="saleAmount" label="" width="100" )
                el-table-column(prop="profitAmoun" label="" width="100" )
                el-table-column(prop="bonusBook" label="" width="100" )
                el-table-column(prop="bonus" label="" width="100" )
                el-table-column(prop="" label="" align="center" width="80")
                   template(scope="scope")
                    span(:class="{ 'text-green': scope.row.isDone > 0, 'text-blue': scope.row.isDone < 1 }") {{ ['', ''][scope.row.isDone]}}
                el-table-column(prop="userpoint" label="" align="center")
                    template(scope="scope")
                      .ds-button.text-button.blue(style="padding: 0 .05rem" @click="goContractDetail(scope.row.contractId)") 

          el-table-column(prop="issue" label="" width="140")
          el-table-column(prop="date" label="" width="140" )
          el-table-column(prop="uTime" label="" width="140" )
          el-table-column(prop="monthlyBuy" label="" width="140" )
          el-table-column(prop="monthlyProfit" label="" width="140" )
          el-table-column(prop="bookProfit" label="" width="140" )
          el-table-column(prop="totalProfit" label="" width="140" )
          el-table-column(prop="shareBook" label="" width="140" )
          el-table-column(prop="shareAmount" label="" width="140" )
          el-table-column(label="" align="center" width="80")
            template(scope="scope")
              span {{ ['', ''][scope.row.bonusType - 1]}}

          el-table-column(label="" align="center" width="80")
            template(scope="scope")
              span {{ ['', ''][scope.row.isGrand]}}


          el-table-column(prop="" label="" align="center" width="80")
             template(scope="scope")
              span(:class="{ 'text-green': scope.row.isSend > 0, 'text-blue': scope.row.isSend < 1 }") {{ ['', ''][scope.row.isSend]}}
      


      
</template>

<script>
  import api from '../../http/api'
  import store from '../../store'
  import { dateTimeFormat } from '../../util/Date'
  export default {
    data () {
      return {
        me: store.state.user,
        // 0 
        // 1 
        type: 0,
        st: '',
        et: '',
        // 
        STATUS: [
          {id: 0, title: '', class: 'waiting-pay'},
          {id: 1, title: '', class: 'wait'},
          {id: 2, title: '', class: 'paid'},
          {id: 3, title: '', class: 'paid-out'}
        ],
        s: {},
        bonusList: [],
        topBonuList: [],
        topDetailList: []
      }
    },
    computed: {
      apiBonus () {
        return this.me.role < 4 ? [api.topBonus, api.mySubBouns][this.type] : [api.myBonus, api.mySubBouns][this.type]
      }
    },
    watch: {
      type () {
        this.bonus()
      }
    },
    mounted () {
      this.bonus()
    },
    methods: {
      expand (row, expanded) {
        if (expanded && !row.topDetailList) {
          this.topBonuDetail(row)
        }
      },
      topBonuDetail (row) {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(api.topBonuDetail, {
          issue: row.issue
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            this.topDetailList = data.topDetailList
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      },
      goContractDetail (id) {
        this.$router.push({
          path: '/group/3-3-4',
          query: {id: id}
        })
      },
      goStockDetail (id) {
        this.$router.push({
          path: '/group/3-3-2',
          query: {id: id, self: !this.type}
        })
      },
      bonus () {
        let loading = this.$loading({
          text: '...',
          target: this.$el
        }, 10000, '...')
        this.$http.get(this.apiBonus, {
          startDate: this.st ? dateTimeFormat(this.st.getTime()).replace(/[\s:-]*/g, '') : '',
          endDate: this.et ? dateTimeFormat(this.et.getTime()).replace(/[\s:-]*/g, '') : '',
          status: this.s.id || ''
        }).then(({data}) => {
          // success
          if (data.success === 1) {
            let bonus = data.myBonus || data.mySubBonus
            this.bonusList = bonus
            data.topBonuList && (this.topBonuList = data.topBonuList)
            setTimeout(() => {
              loading.text = '!'
            }, 100)
          } else loading.text = '!'
        }, (rep) => {
          // error
          this.$message.error('')
        }).finally(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
      }
      // 
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=myBonus&startDate=20170101200000&endDate=20170115200000
      // 
      // http://192.168.169.44:9901/cagamesclient/team/contractBonus.do?method=mySubContract&issue=20170301&sendType=1

    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .stock-list
    top TH
    .form
      padding 0 PWX

  .item
    display inline-block
    margin 0 PW .1rem 0

    
  .el-select
  .el-input-number
    width 1rem

</style>


